import React, { Component } from 'react';
import PropTypes from 'prop-types'; //PropTypes
import * as RecordAPI from '../utils/RecordsAPI';

export default class Record extends Component {
  constructor() {
    super();
    this.state = { edit: false };
  }

  handleToggle() {
    this.setState({ edit: !this.state.edit });
  }

  handleEdit(e) {
    //
    e.preventDefault();
    const record = {
      date: this.refs.date.value,
      title: this.refs.title.value,
      amount: Number(this.refs.amount.value)
    };

    RecordAPI.update(this.props.record.id, record)
      .then(res => {
        this.props.handleEditRecord(this.props.record, res.data.data);
        this.setState({ edit: false });
      })
      .catch(error => console.log(error.message));
  }

  handleDelete(e) {
    e.preventDefault();
    RecordAPI.remove(this.props.record.id)
      .then(res => this.props.handleDeleteRecord(this.props.record, res.data))
      .catch(error => console.log(error.message));
  }

  recordRow() {
    //
    return (
      <tr>
        <td>{this.props.record.date}</td>
        <td>{this.props.record.title}</td>
        <td>{this.props.record.amount}</td>
        <td>
          <button
            className="btn btn-info mr-2"
            onClick={this.handleToggle.bind(this)}
          >
            
          </button>
          <button
            className="btn btn-danger"
            onClick={this.handleDelete.bind(this)}
          >
            
          </button>
        </td>
      </tr>
    );
  }
  recordForm() {
    //
    return (
      <tr>
        <td>
          <input
            type="text"
            ref="date"
            defaultValue={this.props.record.date}
            className="form-control"
          />
        </td>
        <td>
          <input
            type="text"
            ref="title"
            defaultValue={this.props.record.title}
            className="form-control"
          />
        </td>
        <td>
          <input
            type="text"
            ref="amount"
            defaultValue={this.props.record.amount}
            className="form-control"
          />
        </td>
        <td>
          <button
            className="btn btn-info mr-2"
            onClick={this.handleEdit.bind(this)}
          >
            
          </button>
          <button
            className="btn btn-danger"
            onClick={this.handleToggle.bind(this)}
          >
            
          </button>
        </td>
      </tr>
    );
  }
  render() {
    if (this.state.edit) {
      return this.recordForm();
    } else {
      return this.recordRow();
    }
  }
}

Record.propTypes = {
  id: PropTypes.number,
  date: PropTypes.string,
  title: PropTypes.string,
  amount: PropTypes.number
};



