import React, { Component } from 'react';
import Record from './Record';
//import $ from 'jquery'; //
// import axios from 'axios'; //2.
import * as RecordsAPI from '../utils/RecordsAPI';
import RecordForm from './RecordForm';
import AmountBox from './AmountBox';
export default class Records extends Component {
  constructor() {
    super();
    this.state = { error: null, isLoaded: false, records: [] };
  }
  // -
  componentDidMount() {
    //jquery
    // $.getJSON(
    //   'https://5b45a86ad06e180014f9d64d.mockapi.io/api/v1/recordList'
    // ).then(
    //   response =>
    //     this.setState({
    //       records: response,
    //       isLoaded: true
    //     }),
    //   error =>
    //     this.setState({
    //       isLoaded: true,
    //       error:error.responseText
    //     })
    // );

    //axios
    RecordsAPI.getAll()
      .then(res => {
        this.setState({
          records: res.data,
          isLoaded: true
        });
      })
      .catch(error =>
        this.setState({
          isLoaded: true,
          error: error.message
        })
      );
  }
  addRecord(record) {
    //
    this.setState({
      error: null,
      isLoaded: true,
      records: [...this.state.records, record]
    });
  }

  updateRecord(record, data) {
    //
    const recordIndex = this.state.records.indexOf(record);
    const newRecord = this.state.records.map((item, index) => {
      if (index !== recordIndex) {
        return item;
      }
      return {
        ...item,
        ...data
      };
    });
    this.setState({ records: newRecord });
  }

  deleteRecord(record, data) {
    //
    const recordIndex = this.state.records.indexOf(record);

    const newRecords = this.state.records.filter(
      //
      (item, index) => index !== recordIndex
    );
    this.setState({ records: newRecords });

    console.log(record);
  }

  credits() {
    //
    let credits = this.state.records.filter(record => {
      return record.amount >= 0;
    });

    return credits.reduce((prev, curr) => {
      return prev + Number(curr.amount);
    }, 0);
  }

  debits() {
    //
    let credits = this.state.records.filter(record => {
      return record.amount < 0;
    });

    return credits.reduce((prev, curr) => {
      return prev + Number(curr.amount);
    }, 0);
  }

  balance() {
    return this.credits() + this.debits();
  }
  render() {
    const { error, isLoaded, records } = this.state;
    let recordComponent;
    if (error) {
      recordComponent = <div>Error:{error}</div>;
    } else if (!isLoaded) {
      recordComponent = <div>...</div>;
    } else {
      recordComponent = (
        <div>
          <table className="table table-bordered">
            <thead>
              <tr className="text-cencer">
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {records.map(record => (
                <Record
                  key={record.id}
                  record={record}
                  handleEditRecord={this.updateRecord.bind(this)}
                  handleDeleteRecord={this.deleteRecord.bind(this)}
                />
              ))}
            </tbody>
          </table>
        </div>
      );
    }

    return (
      <div>
        <h4 className="mt-2"></h4>
        <div className="row mr-3 mb-2">
          <AmountBox text="" type="success" amount={this.credits()} />
          <AmountBox text="" type="danger" amount={this.debits()} />
          <AmountBox text="" type="info" amount={this.balance()} />
        </div>
        <RecordForm handleNewRecord={this.addRecord.bind(this)} />
        {recordComponent}
      </div>
    );
  }
}



