import React, { Component } from 'react';
import * as RecordAPI from '../utils/RecordsAPI';
export default class RecordForm extends Component {
  constructor(props) {
    super(props);
    this.state = { date: '', title: '', amount: '' };
  }

  valid() {
    return this.state.date && this.state.title && this.state.amount;
  }
  handleChange(e) {
    let name, obj;
    name = e.target.name;
    this.setState(((obj = {}), (obj['' + name] = e.target.value), obj));
  }

  handleSubmit(e) {
    e.preventDefault();
    const data = {
      date: this.state.date,
      title: this.state.title,
      amount: Number(this.state.amount)
    };

    RecordAPI.create(data)
      .then(res => {
        console.log(res.data);
        this.props.handleNewRecord(res.data.data);
        this.setState({ date: '', title: '', amount: '' });
      })
      .catch(error => console.log(error.message));
  }

  render() {
    return (
      <form
        className="form-inline mb-3 mt-1"
        onSubmit={this.handleSubmit.bind(this)}
      >
        <div className="form-group mr-2">
          <input
            type="text"
            className="form-control"
            name="date"
            placeholder=""
            value={this.state.date}
            onChange={this.handleChange.bind(this)}
          />
        </div>
        <div className="form-group mr-2">
          <input
            type="text"
            className="form-control"
            name="title"
            placeholder=""
            value={this.state.title}
            onChange={this.handleChange.bind(this)}
          />
        </div>
        <div className="form-group mr-2">
          <input
            type="number"
            className="form-control"
            name="amount"
            placeholder=""
            value={this.state.amount}
            onChange={this.handleChange.bind(this)}
          />
        </div>
        <button
          type="submit"
          className="btn btn-primary"
          disabled={!this.valid()}
        >
          
        </button>
      </form>
    );
  }
}



