import axios from 'axios';
const api =
  process.env.REACT_APP_RECORDS_API_URL ||
  'https://www.sieia.org/index/react/show';

export const getAll = () => axios.get(`${api}/react/show`);

export const create = body => axios.post(`${api}/react/create`, body);

export const update = (id, body) =>
  axios.put(`${api}/react/update?id=${id}`, body);

export const remove = id => axios.delete(`${api}/react/delete?id=${id}`);



